<template>
    <div class="container">
        <h1>Booking Details</h1>
        <hr/>
        <div class="d-flex justify-content-center" v-if="loading">
            <div class="spinner-border" role="status">
                <span class="sr-only">Loading...</span>
            </div>
        </div>
        <div class="alert alert-danger" role="alert" v-if="error">
            {{ error.message }}
        </div>
        <!-- {{ bookings.bookings[0].userID }} -->
        <div v-if="!loading && !error && items.length !==0">
            <b-table
                :fields="fields"
                :items="items"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                responsive
            >
                <template #cell(index)="data">
                        {{ data.index + 1 }}
                </template>
            </b-table>
        </div>
    </div>
</template>

<script>
// import Vue from 'vue';
import {getBookings} from '@/services/bookings';
export default {
    name : 'BookingDetails',
    data(){
        return{
            sortBy:'bookingDate',
            sortDesc:false,
            fields:['index',
                {key:'userID'},{key:'providerID'}, 'serviceID', 
                {key:'bookingDate', sortable:true},
                'bookingTime',
                {key:'serviceCost' ,sortable:true}, 
                {key:'serviceStatus', sortable:true}, 
                {key:'paymentStatus', sortable:true }
        ],
            items:[],
            loading:this.loading,
            error:this.error,
        }
    },
    methods:{
       
    },
    async mounted(){
        this.loading = true;
        try{
            const bookings = await getBookings();
            this.items = bookings.bookings;  
        }catch(error){
            this.error = error;
        }finally{
            this.loading = false;
        }
    },
};
</script>

